.goal {
    @apply flex p-2 justify-between;
}

.icon {
    @apply h-10 w-10 nm-inset-gray-100 rounded-full flex items-center justify-center text-xl;
}

.greybar {
    @apply nm-convex-gray-100 h-1 w-28 rounded bg-blue-200;
}

.bluebar {
    @apply h-1 rounded bg-gradient-to-l from-green-200 to-blue-500;
}